.dark{
    background-color: rgb(5, 5, 5);
}
.container{
    width:15em;
    margin:0 0.2em;
    float:left;
    position:relative;
}
.container .darkMode{
color:white;

}
.container .lightMode{
    color:black

}
.pic img{
    width:100%;
}
.title{
    font-weight:bold;
    margin-bottom:1em;

}
.channel, .views{
font-size:large;
color:rgba(241, 234, 234, 0.912);
margin-bottom:0.2em;
}
.views span{
    padding:0 0.5em;
}
.close{
    position:absolute;
    right:0;

}
.edit{
    position:absolute;
    right:3em;
}